import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import './i18n';
import { BrowserRouter } from 'react-router-dom';


ReactDOM.hydrate((
    <React.Fragment>
        <BrowserRouter>
            <App/>
        </BrowserRouter>
    </React.Fragment>),
 document.getElementById('root'));

//ReactDOM.render(<App />, document.getElementById('root'));



